
body {
  background: rgb(66, 70, 90);
  display: flex;
  align-items: center;
  justify-content: center;
}
.App {
  height: 700px;
  width: 400px;
}
.Display {
  border: 1px solid #321c58;
  background: #321c58;
  color: #ffffff; 
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 0 15px ;
  height: 10%;
  overflow: hidden;
  font-size: 25px;
  position: relative;
}
.Buttons {
  display: flex;
  flex-wrap: wrap;
  flex-direction:column;
  height: 50%;
}

.Button {
  background: #cad2c5;
  display: flex;
  box-sizing: border-box;
  border: 1px solid #321c58;
  align-items: center;
  justify-content: center;
  width: 25%;
  font-size: 24px;
  color: #321c58;
  height: 20%;
}

.Button[data-size='2']{
  height:40%;
}

.Button:hover {
  background: #d7ddd3;
  cursor: pointer;
}

.Button:active {
  background: #bdc7b7;
}
.Header{
color: rgb(255, 255, 255);
margin-left: -100px;
display: flex;
height: 100px;  
width: 700px;
}
